import { Logger } from '@nestjs/common'
import { NestFactory } from '@nestjs/core'

import { AppModule } from './app/app.module'
import { FLOWDA_GATEWAY_ENV, TrpcRouter } from '@flowda-projects/flowda-gateway-trpc-server'
import { createProxyMiddleware } from 'http-proxy-middleware'
import { ExtractJwt } from 'passport-jwt'
import type { JwtPayload } from 'jsonwebtoken'
import * as jwt from 'jsonwebtoken'

async function bootstrap() {
  const app = await NestFactory.create(AppModule)

  app.enableCors()
  const globalPrefix = 'flowda-gateway-api'
  app.setGlobalPrefix(globalPrefix)

  const trpc = app.get(TrpcRouter)
  trpc.applyMiddleware(app, globalPrefix)

  app.use((req, res, next) => {
    if (req.originalUrl.includes('favicon.ico')) {
      res.status(204).end()
    } else if (req.url.indexOf(`/${globalPrefix}/camunda/engine-rest/`) > -1) {
      const extract = ExtractJwt.fromAuthHeaderAsBearerToken()
      const bearerToken = extract(req)
      if (bearerToken) {
        try {
          const authed = jwt.verify(bearerToken, FLOWDA_GATEWAY_ENV.ACCESS_TOKEN_SECRET) as JwtPayload
          if (authed) {
            next()
          } else {
            res.status(401).json({
              message: '[PROXY] Unauthorized',
            })
          }
        } catch (e) {
          res.status(401).json({
            message: '[PROXY] Unauthorized',
          })
        }
      } else {
        res.status(401).json({
          message: '[PROXY] Unauthorized',
        })
      }
    } else {
      next()
    }
  })

  // Proxy endpoints
  app.use(
    `/${globalPrefix}/camunda/engine-rest`,
    createProxyMiddleware({
      logLevel: 'debug',
      target: FLOWDA_GATEWAY_ENV.C7_REST_URL,
      changeOrigin: true,
      onProxyReq(proxyReq) {
        // camunda rest api  npe
        proxyReq.setHeader('Content-Type', 'application/json')
        proxyReq.setHeader('Accept', 'application/json')
      },
      pathRewrite: {
        [`^/${globalPrefix}/camunda/engine-rest`]: '/engine-rest',
      },
    }),
  )

  const port = process.env.PORT || 3310
  await app.listen(port)
  Logger.log(` Application is running on: http://localhost:${port}/${globalPrefix}`)
}

bootstrap()
